	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Fear and change","date":"15 Feb 2004","slug":"/2004-02-15-fear-and-change/","tags":"Y2004"},"html":"<p>Jeff Jarvis: <a title=\"BuzzMachine... Fear\" href=\"http://www.buzzmachine.com/archives/2004_02.html#006242\">Fear</a> and <a title=\"BuzzMachine... Change and Fear: What they got wrong about America\" href=\"http://www.buzzmachine.com/archives/2004_02.html#006251\">change</a>. I may not agree with all of his conclusions, but I do agree with his reasoning. Blaming the voters or the media or blaming anybody is not the answer. Voters are by definition always right, and if the Democrats want to beat Bush this fall, they have know what the voters want. And they don't want to be told that they are wrong. Outsiders wanting to understand the current mood in the US would do well to read these.</p>"},"next":{"fields":{"title":"Decidedly undecided","date":"15 Feb 2004","slug":"/2004-02-15-decidedly-undecided/"}},"previous":{"fields":{"title":"Square","date":"15 Feb 2004","slug":"/2004-02-15-square/"}}},{"node":{"fields":{"title":"Decidedly undecided","date":"15 Feb 2004","slug":"/2004-02-15-decidedly-undecided/","tags":"Y2004"},"html":"<p>Like <a title=\"NDR Online: Wahltest [Noch'n Blogg]\" href=\"http://blogg.lumma.de/eintrag.php?id=33\">Nico</a>, I decided to take the <a href=\"http://www.ndr.de/ndr/regional/hh/wahl/wahltest/\">'Voter's Test'</a> at NDR4 to determine which party in Hamburg would best reflect my position. The result was amusing. </p>\n<p><img alt=\"ndr4.png\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/02/ndr4.png\" width=\"416\" height=\"234\" border=\"0\" /><br />\n\"It is difficult to place you in a particular party. You correspond almost equally with FDP, CDU, and SPD.\" My politics are middle-of-the-road German (or maybe, to put it another way, simply wishy-washy).</p>\n<p>Of course,  I'm not actually eligible to vote in Hamburg (seeing as I don't live there and am not a German citizen), so I haven't really bothered to formulate exact positions. On law and order I'm rather liberal, I think the harbor and the Airbus plant should be expanded, I don't think the city needs to own hospitals and apartment complexes. That almost sounds like the FDP, except that the FDP is run by a bunch of idiots, especially in Hamburg.</p>"},"next":{"fields":{"title":"Mexican anti-American, pro-terrorist soccer hooligans","date":"14 Feb 2004","slug":"/2004-02-14-mexican-anti-american-pro-terrorist-soccer-hooligans/"}},"previous":{"fields":{"title":"Fear and change","date":"15 Feb 2004","slug":"/2004-02-15-fear-and-change/"}}},{"node":{"fields":{"title":"Mexican anti-American, pro-terrorist soccer hooligans","date":"14 Feb 2004","slug":"/2004-02-14-mexican-anti-american-pro-terrorist-soccer-hooligans/","tags":"Y2004"},"html":"<p><a title=\"Sounds tamer than a Celtic-Rangers match to me\" href=\"http://www.buzzmachine.com/archives/2004_02.html#006241\">BuzzMachine: Mexican anti-American, pro-terrorist soccer hooligans booing the American anthem and yelling \"Osama! Osama!\" at our players.</a></p>"},"next":{"fields":{"title":"Ill Wind Blowing?","date":"14 Feb 2004","slug":"/2004-02-14-ill-wind-blowing/"}},"previous":{"fields":{"title":"Decidedly undecided","date":"15 Feb 2004","slug":"/2004-02-15-decidedly-undecided/"}}}],"pathPrefix":"","first":false,"last":false,"index":529,"pageCount":1101}}

